import React, { Component } from 'react';
import "../serviceCard.css"

const Footer = () => {

    const date = new Date()
    const name = "مركز الخدمه"

    return (  <React.Fragment>
        <footer className="bg-dark fm">
            <div className="contact">
                <p>جميع الحقوق محفوظه {name} {date.getFullYear()}</p>
            </div>
        </footer>
    </React.Fragment> );
}
 
export default Footer ;