import React, { Component  , useState , useCallback} from 'react';
import {Link , NavLink} from "react-router-dom" ;
import "../serviceCard.css"

const Header = () => {

  const [state , setState] = useState(false)

  const toggelNav = useCallback(()=>{
    if(state){
      setState(false)
    }else{
      setState(true)
    }
  })



  return ( 
        <React.Fragment>
      <nav className=" navbar navbar-expand-lg navbar-dark bg-dark" dir="rtl">
      <div className="container-fluid">
        <a className="navbar-brand fs-4">مركز صيانه</a>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item m-2">
              <NavLink className="nav-link  fw-bold fs-6" to="/">
              <i className="fas fa-home"></i> 
                الرئيسيه</NavLink>
            </li>
            <li className="nav-item m-2">
              <NavLink className="nav-link fw-bold fs-6" to="/services">
              <i className="fas fa-tools"></i>
                خدمات الصيانه</NavLink>
            </li>
          </ul>
        </div>
        {/* phone */}
      <button className="navbar-toggler" 
              type="button" data-bs-toggle="collapse" 
              data-bs-target="#navbarToggleExternalContent" 
              aria-controls="navbarToggleExternalContent" 
              aria-expanded="false" 
              aria-label="Toggle navigation"
              onClick = {toggelNav}
              >
        <span className="navbar-toggler-icon"></span>
      </button>
      {/* phone */}
        <div className="m-3 order">
        <Link className="btn btn-light fw-bold fs-6" to="/order/0">
            طلب صيانه</Link>
        </div>
      </div>
      {/* mobile navigation */}
      <div className={`mobile-nav bg-dark ${state?"open":""}`}>
          <ul className="navbar-nav">
            <li className="nav-item m-2" onClick = {toggelNav}>
              <NavLink className="nav-link  fw-bold fs-6" to="/">
              <i className="fas fa-home"></i> 
                الرئيسيه</NavLink>
            </li>
            <li className="nav-item m-2" onClick = {toggelNav}>
              <NavLink className="nav-link fw-bold fs-6" to="/services">
              <i className="fas fa-tools"></i>
                خدمات الصيانه</NavLink>
            </li>
          </ul>
      </div>
    </nav>
    </React.Fragment>
   );
}

export default Header ;