import React, { Children, Component } from 'react';
import { Route, Routes ,useParams} from "react-router-dom";



import Header from "./header"
import Home from "./home"
import Services from "./services"
import ServiceBook from "./serviceBook"
import Footer from "./footer"
import NotFound from "./notfound"


// import UserProfile from "./userProfile"
class App extends Component {

    state = {
        serviceitem : [
            {name : "الثلاجات" , image : "ref.jpg" , key: 1} ,
            {name : "الغسالات" , image : "wm.jpg" , key: 2} ,
            {name : "السخان" , image : "wh.jpg" , key:3} ,
            {name : "البوتاجاز" , image : "stove.jpg" , key: 4} ,
            {name : "الشاشات" , image : "tv.jpg" , key: 5} ,
            {name : "الاجهزه المنزليه الصغيره" , image : "sha.jpg" , key: 6} ,
            {name : " التكييف" , image : "ac.jpg" , key: 7} ,
            {name : " الافران" , image : "oven.jpg" , key: 8} ,
        ]
    }

    dynamicSelect = (e)=>{
        // clon
        let value = this.state.selectValue
        // edit
        value = e
        // set state
        this.setState({selectValue : value})
    }

    render() {
        return(
            <React.Fragment>
                
                <main dir="rtl" className="main">
                    <Header/>
                    <Routes>
                        <Route path="/" element={<Home/>}/>
                        <Route path="/services" element={<Services serviceitem={this.state.serviceitem} dynamic={this.dynamicSelect}/>} /> 
                        <Route path="/order/:id" element={<ServiceBook serviceitem={this.state.serviceitem} serviceitem={this.state.serviceitem} dynamic={this.dynamicSelect} selectValue={this.state.selectValue}/>} />
                        <Route path="*" element={<NotFound/>} /> 
                    </Routes>
                    <Footer/>
                </main>
                
            </React.Fragment>
        )
    }
}

export default App ;