import React, { Component,useState,useCallback, useEffect} from 'react';
import {useParams} from "react-router-dom"




const ServiceBook = (props) => {

    // get params
    const params = useParams()

    const [inputvalue , setInputValue] = useState({
        clintName : "" , 
        clintPhone : "" ,
        clintAdress : "" ,
        product : 0,
        discription :""
    })

    const [validate , setValidate] = useState([])

    const onChangehanler = useCallback((e)=>{
        // clone 
        let values = {...inputvalue} 
        // edit
        values[e.currentTarget.name] = e.currentTarget.value
        // set state
        setInputValue(values)
    })

    const valedate = useCallback(()=>{
        // clone 
        let inputVal = {...inputvalue} ;
        // edit
        if(inputVal.clintName != "" ){
            return ;
        }else{
            validate.push("this input cant by empty and it cant take number")
        }
        if(inputVal.clintPhone != ""){
            return;
        }else{
            validate.push("this input cant by empty")
        }
        if(inputVal.clintAdress != ""){
            return;
        }else{
            validate.push("this input cant by empty")
        }
        if(inputVal.product != 0){
            return;
        }else{
            validate.push("please select product")
        }
        if(inputVal.discription != ""){
            return;
        }else{
            validate.push("this input cant by empty")
        }
        setInputValue(validate)
    })

    const handelSubmet = useCallback((e)=>{
        e.preventDefault() ;
        valedate();
        if(validate.length === 0){
            console.log("submit")
        }else{
            console.log(validate)
        }
    })

    return ( 
        <React.Fragment>
            <div className="container">
            <h3 className="text-center m-4">طلب صيانه</h3>
            <form   onSubmit={handelSubmet}>
                <div className="mb-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">اسم العميل</label>
                    <input 
                        name = "clintName"
                        onChange= {onChangehanler}
                        type="text" 
                        className="form-control" 
                        id="exampleInputEmail1"/>
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">رقم الهاتف</label>
                    <input 
                        name = "clintPhone"
                        onChange= {onChangehanler}
                        type="number" 
                        className="form-control" 
                        id="exampleInputPassword1"/>
                </div>
                <div className="mb-3">
                    <label htmlFor="adress" className="form-label">العنوان</label>
                    <input 
                        name = "clintAdress"
                        onChange= {onChangehanler}
                        type="text" 
                        className="form-control" 
                        id="adress"/>
                        
                </div>
                <div className="mb-3">
                    <label className="form-label">اختر المنتج</label>
                    <select 
                        name="product" 
                        className="form-select" 
                        aria-label="Default select example">
                        <option value="0"> </option>
                        {props.serviceitem.map(item => (
                            <option key={item.key} value={item.key}>{item.name}</option>
                        ))}
                    </select>
                </div>
                <div className="mb-3">
                    <label htmlFor="issu" className="form-label">سبب الصيانه</label>
                    <textarea 
                        name="discription"
                        onChange= {onChangehanler}
                        className="form-control" 
                        id="issu"/>
                </div>
                <button 
                    type="submit" 
                    className="btn btn-primary" >Submit</button>
            </form>
            </div>
        </React.Fragment>
    );
}
export default ServiceBook ;